html {
  background: #557085;
}

section#login {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Noto Sans KR", sans-serif;
  position: relative;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.5s;
}

@media (min-width: 320px) {
  section#login .login_container {
    position: relative;
    width: 100vw;
    height: 40vh;
    background: #fff;
    box-shadow: 0 15px 50px rgba(0, 0, 0, 1);
  }
}

@media (min-width: 600px) {
  section#login .login_container {
    position: relative;
    width: 800px;
    height: 500px;
    background: #fff;
    box-shadow: 0 15px 50px rgba(0, 0, 0, 1);
  }
}

section#login .login_user_signinBx {
  position: absolute;
  min-height: 240px;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

section#login .login_container .login_user_signinBx .login_imgBx {
  position: relative;
  width: 50%;
  height: 100%;
  transition: 0.5;
}

section#login .login_container .login_user_signinBx img#login_back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0;
  transition: opacity 2s;
}

section#login .login_container .login_user_signinBx .login_formBx {
  width: 50%;
  height: 100%;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0% 10%;
  transition: 0.5s;
}

section#login .login_container .login_user_signinBx .login_formBx h2 {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  width: 100%;
  margin-bottom: 5%;
  color: #555;
}

section#login .login_container .login_user_signinBx .login_formBx input {
  width: 94%;
  height: 36px;
  padding: 0% 0% 0% 6%;
  background: #f5f5f5;
  color: #333;
  border: none;
  outline: none;
  box-shadow: none;
  font-size: 14px;
  margin: 5% 0%;
  letter-spacing: 1px;
  font-weight: 300;
}

section#login
  .login_container
  .login_user_signinBx
  .login_formBx
  input[type="submit"] {
  width: 35%;
  position: relative;
  padding: 0;
  left: 65%;
  background: #677eff;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: 0.5s;
}

section#login .login_container .login_user_signinBx .login_formBx .login_p {
  font-size: 12px;
  letter-spacing: 1px;
  color: #555;
  text-transform: uppercase;
  font-weight: 300;
}
section#login .login_container .login_user_signinBx .login_formBx .login_p a {
  font-weight: 600;
  text-decoration: none;
  color: #577eff;
}

header.nav {
  font-family: "Noto Sans KR", sans-serif;
  background: black;
  position: relative;
  max-width: 1200px;
  min-width: 240px;
  padding: 10px;
  box-sizing: border-box;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

header.nav .Login {
  color: #88622b;
  height: 60px;
  font-size: 36px;
  line-height: 60px;
  padding: 0 20px;
  text-align: center;
  box-sizing: border-box;
  float: left;
  font-weight: 700;
  text-decoration: none;
}

header.nav nav {
  float: right;
}

header.nav .clearfix {
  clear: both;
}

header.nav nav ul {
  margin: 0;
  padding: 0;
  display: flex;
}

header.nav nav ul li {
  list-style: none;
}

header.nav nav ul li a {
  display: block;
  margin: 10px 3px;
  padding: 10px 20px;
  text-decoration: none;
  color: #cdbe91;
}

header.nav nav ul li a.active {
  color: #fff7df;
  border-style: solid;
  margin: 7px 0;
  border-image: linear-gradient(0.25turn, #2b271f, #cabb8f, #2b271f) 1;
  border-image-width: 0 0 3px 0;
}

header.nav nav ul li a:hover {
  color: #fff7df;
}

@media (max-width: 540px) {
  header.nav .menu {
    display: block;
    width: 40px;
    height: 40px;
    margin: 10px;
    float: right;
    cursor: pointer;
    background: url(data:image/png;base64,);
    background-size: contain;
  }
  header.nav .arrow {
    background: url(data:image/png;base64,);
    background-size: contain;
  }

  header.nav {
    height: 80px;
    overflow: hidden;
    transition: height 0.3s ease-in-out;
  }

  header.open {
    height: 130px;
  }
  header.nav nav.open ul {
    display: block;
  }
  header.nav nav.open li {
    margin: 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
}

